import React, { useState, useEffect } from 'react';
import Container from 'react-bootstrap/Container';
import Jumbotron from 'react-bootstrap/Jumbotron';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Badge from 'react-bootstrap/Badge';
import { Nav } from 'react-bootstrap';
import Toast from 'react-bootstrap/Toast';
import './Shop.css';

export function Shop({ addItem }) {

    useEffect(() => {
        fetchBeers();
        return null;
    }, []);

    const [beers, setBeers] = useState([]);

    const fetchBeers = async () => {
        const data = await fetch(`https://api.punkapi.com/v2/beers?per_page=12`);

        const items = await data.json();

        setBeers(items);
    }

    const handleAdd = (id) => {
      
        console.log(id);
        const beer = beers.filter((beer) => beer.id == id );
       console.log(beer);
     addItem(beer[0]);
    }


    return (
        <Container className='mt-auto'>

            <div className='shop-header ' ><h3> Amazing <span className='underline--magical '>beers</span> <span className='sub'>(for amazing people)</span></h3></div>

            <div className="d-flex flex-row  justify-content-center flex-wrap">
                {beers.map(beer => {
                    return (
                        <Card key={beer.id} bg='light' style={Styles.Card} className='shop-shadow'>
                            <Card.Header style={Styles.Cardheader}>{beer.tagline}</Card.Header>
                            <Card.Img variant="top" src={beer.image_url} style={Styles.CardImg} />
                            <Card.Body>
                                <Card.Title>{beer.name}</Card.Title>

                                <Card.Text className='text-justify' style={Styles.Cardtext}>
                                    <b>Description:</b> {beer.description}
                                </Card.Text>
                            </Card.Body>
                            <Card.Footer className='shop-card-footer'>
                                <Nav.Link href={`/shop/${beer.id}`}> <Button className='shop-button' variant="dark" style={Styles.Button}>Discover</Button></Nav.Link>
                                <Badge variant='dark' pill id={beer.id} onClick={() => handleAdd(beer.id)} className=' ml-5 p-2 shop-cart'><i className='fas fa-shopping-cart' /></Badge>
                            </Card.Footer>

                        </Card>
                    )
                })}
            </div>



        </Container>
    )
}

const Styles = {
    Card: { width: '16rem', margin: 10, opacity: 0.93 },
    CardImg: {
        width: '3.6rem',
        height: '10rem',
        alignSelf: 'center',
        marginTop: 10,
        marginBottom: -15,
    },
    Cardheader: { fontWeight: "bolder" },
    Cardtext: {
        maxHeight: '23vh',
        overflowY: 'scroll',
        fontSize: '0.8rem'
    },
    jumboStyle: {
        opacity: 1,
        backgroundColor: 'rgba(0,0,0,0.6)',
        marginTop: 10,
        padding: 10,
        color: 'white'
    },

    Button: {
        margin: -15
    },

    price: {
        fontSize: '0.8rem'
    }
}